import React, { useState, useEffect, useRef } from 'react';
import styles from './header.module.css';
import Logo from '../../../assets/logo.png'
import { Link } from 'gatsby';
import { Rotate as Hamburger } from 'hamburger-react'

const NavBar = ( props ) => {
    const [isScrolled, setScrolled] = useState(false);
    const node = useRef()

    const onScroll = () => {
        const scroll = window.scrollY
        
        if (window.scrollY > 0) {
            if (!isScrolled) {
                setScrolled(true);
            }
        }
        else {
            if (isScrolled) {
                setScrolled(false);
            }
        }
    }


    const handleClick = (e) => {
        if (node.current.contains(e.target)) {
            // inside click
            return;
        }
        if (props.outsideClick) {
            props.outsideClick()
        }
    }


    useEffect(() => {
        document.addEventListener("mousedown", handleClick);   
        document.addEventListener("scroll", onScroll)
        
        return () => {
            document.removeEventListener("mousedown", handleClick);
            document.removeEventListener("scroll", onScroll)
        }

    })

    return (
        <div ref={node} className={props.className + " z-50 sticky top-0 w-full"}>
            <div className={styles.shadowBG + (isScrolled ? " opacity-100" : " opacity-0") + " absolute top-0 left-0 right-0 bottom-0 bg-red-300"} />
            <div className="padding z-10 bg-white flex items-center justify-between ">
                
                {props.children}
                
            </div>
            {props.menu}
        </div>
    )
}

const Header = ( props ) => {

    const [isCompact, setCompact] = useState(false);
    const [isMenuOpen, setMenuOpen] = useState(false);
    
    useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth <= 500) {
                if (!isCompact) {
                    setCompact(true);
                }
            }
            else {
                if(isCompact) {
                    setCompact(false)
                }
            }
        }
    
        handleResize();

        window.addEventListener("resize", handleResize)

        return () => {
            window.removeEventListener("resize", handleResize)
        }
    })

    const didToggleMenu = (open) => {
        setMenuOpen(open);
    }

    if (isCompact) {
        const menu = (
            <div className={"bg-white absolute mt-10 top-0 left-0 right-0 pt-2 pb-3 flex flex-col " + styles.dropdownShadow + " " + styles.menu}>
                <div className="w-full separator bg-gray-200 mb-2 mt-1" />
                <Link to="/" onClick={()=>setMenuOpen(false)} className={props.selectedIndex === 0 ? styles.selectedNavItem : styles.navItem}>Home</Link>
                <Link to="/features/" onClick={()=>setMenuOpen(false)} className={props.selectedIndex === 1 ? styles.selectedNavItem : styles.navItem}>Features</Link>
                <Link to="/faq/" onClick={()=>setMenuOpen(false)} className={props.selectedIndex === 2 ? styles.selectedNavItem : styles.navItem}>FAQ</Link>
                <a href="mailto:contact@andante.app" onClick={()=>setMenuOpen(false)} className={styles.navItem}>Contact Me</a>
            </div>
        );
        return (
            <NavBar menu={isMenuOpen && menu} outsideClick={()=>setMenuOpen(false)}>
                <Link to="/" onClick={()=>setMenuOpen(false)} className="w-8 h-8">
                    <img src={Logo} alt='Andante Logo'/>
                </Link>
                <div className="-mr-3">
                    <Hamburger
                        label="Show menu"
                        rounded
                        color="#2d3748" 
                        size={22}
                        duration={0.3}
                        direction="left"
                        toggled={isMenuOpen} 
                        toggle={didToggleMenu} />
                </div>
                
            </NavBar>
        )
    }
    else {
        return (
            <NavBar>
                <div className="flex py-2 sm:py-4">
                    <Link to="/" className="flex-none mr-4 w-8 h-8">
                        <img src={Logo} alt='Andante Logo'/>
                    </Link>
                    
                    <Link to="/" className={"py-1 text-gray-800 text-base font-semibold"}>Andante</Link>
                </div>
                <div className="-mr-4 py-2 sm:py-4">
                    <Link to="/features/" className={props.selectedIndex === 1 ? styles.selectedNavItem : styles.navItem}>Features</Link>
                    <Link to="/faq/" className={props.selectedIndex === 2 ? styles.selectedNavItem : styles.navItem}>FAQ</Link>
                    <a href="mailto:contact@andante.app" className={styles.navItem}>Contact Me</a>
                </div>
            </NavBar>
        )
    }

}

export default Header;